import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';

import { AppConfig } from '../../app/app.config';
import axios from 'axios'; 

/**
 * Generated class for the NewPostPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-new-post',
  templateUrl: 'new-post.html',
})
export class NewPostPage {

  public displayStatus: string = 'displayForm';

  public visibleRange: number = 0;

  public body: string = '';

  public address:string =  '66/2 Falcon Way, Tweed Heads South NSW 2486';

  private createPostUrl:string = AppConfig.getUrl() + '/api/post/create';

  public postFail: boolean = false;
  public postFailMsg: string = '';

  public latitude: number;

  public longitude: number;

  constructor(public navCtrl: NavController, public navParams: NavParams, private geolocation: Geolocation) {
    this.latitude = navParams.get('latitude');
    this.longitude = navParams.get('longitude');
  }

  ionViewDidLoad() {
    // console.log(this.latitude, this.longitude);
  }

  onPostButtonClick() {
    if (!this.latitude || !this.longitude) {
      this.postFailMsg = 'Not get position information!';
      this.postFail = true;

      return;
    }

    if (this.body.trim() == "") {
      this.postFailMsg = 'Please type post content!';
      this.postFail = true;

      return;
    }

    this.postFail = false;

    // this.navCtrl.pop();
    this.displayStatus = 'displayResult';
  }

  initForm() {
    this.visibleRange = 10;
    this.body = '';
  }

  startNew() {
    this.initForm();
    this.displayStatus = "displayForm";
  }

  backHome() {
    this.navCtrl.popToRoot();
  }

}



